<template>
  <div>
    <a-breadcrumb separator="">
      <a-breadcrumb-item>
        <router-link to="/store/index">当前位置：首页</router-link>
      </a-breadcrumb-item>
      <a-breadcrumb-separator>></a-breadcrumb-separator>
      <a-breadcrumb-item> 花名和特征检索 </a-breadcrumb-item>
    </a-breadcrumb>
    <a-divider></a-divider>
    <a-input-search
      placeholder="您可以输入花名"
      enter-button
      @search="onSearch"
    />
    <div class="atree_list">
      <div class="trees">
        <div>
          <div>花</div>
          <a-tree
            v-model="checkedKeys1"
            checkable
            :expanded-keys="expandedKeys1"
            :auto-expand-parent="autoExpandParent"
            :selected-keys="selectedKeys1"
            :tree-data="treeData1"
            @expand="onExpand1"
            @select="onSelect1"
          />
        </div>
        <div>
          <div>叶</div>
          <a-tree
            v-model="checkedKeys2"
            checkable
            :expanded-keys="expandedKeys2"
            :auto-expand-parent="autoExpandParent"
            :selected-keys="selectedKeys2"
            :tree-data="treeData2"
            @expand="onExpand2"
            @select="onSelect2"
          />
        </div>
      </div>
      <div class="alist">
        <a-list
          item-layout="vertical"
          size="large"
          :pagination="pagination"
          :data-source="listData"
        >
          <a-list-item slot="renderItem" key="item.id" slot-scope="item">
            <img slot="extra" width="272" alt="logo" :src="item.img" />
            <a-list-item-meta :cat="item.cat">
              <a slot="title" :href="item.href" style="font-weight: bold">{{
                item.name_ch
              }}</a>
              <a-avatar slot="avatar" :src="item.img" />
            </a-list-item-meta>
            <p>
              <span style="font-weight: bold; color: #44ae88">生物学分类</span
              >：{{ item.cat }}
            </p>
            <span style="font-weight: bold; color: #44ae88">植物信息：</span
            >{{ item.content }}
            <a style="color: #44ae88; font-weight: bold" @click="postDetail(item.name_ch)"
              ><router-link to="/detail">更多信息</router-link></a
            >
          </a-list-item>
        </a-list>
      </div>
    </div>
  </div>
</template>
<script>
const treeData1 = [
  {
    title: "花",
    key: "花",
    children: [
      {
        title: "花序",
        key: "花序",
        children: [
          { title: "单生花", key: "单生花" },
          { title: "总状花序", key: "总状花序" },
          { title: "伞形花序", key: "伞形花序" },
          { title: "穗形花序", key: "穗形花序" },
          { title: "圆锥花序", key: "圆锥花序" },
        ],
      },
      {
        title: "花色",
        key: "花色",
        children: [
          { title: "红色", key: "红色" },
          { title: "蓝色", key: "蓝色" },
          { title: "粉红色", key: "粉红色" },
          { title: "黄色", key: "黄色" },
          { title: "紫色", key: "紫色" },
          { title: "绿色", key: "绿色" },
          { title: "白色", key: "白色" },
        ],
      },
      {
        title: "花期",
        key: "花期",
        children: [
          { title: "1", key: "1" },
          { title: "2", key: "2" },
          { title: "3", key: "3" },
          { title: "4", key: "4" },
          { title: "5", key: "5" },
          { title: "6", key: "6" },
          { title: "7", key: "7" },
          { title: "8", key: "8" },
          { title: "9", key: "9" },
          { title: "10", key: "10" },
          { title: "11", key: "11" },
          { title: "12", key: "12" },
        ],
      },
    ],
  },
];
const treeData2 = [
  {
    title: "叶",
    key: "叶",
    children: [
      {
        title: "单叶",
        key: "单叶",
      },
      {
        title: "羽状复叶",
        key: "羽状复叶",
      },
      {
        title: "掌状复叶",
        key: "掌状复叶",
      },
      {
        title: "三出复叶",
        key: "三出复叶",
      },
      {
        title: "单身复叶",
        key: "单身复叶",
      },
      {
        title: "簇生",
        key: "簇生",
      },
      {
        title: "互生",
        key: "互生",
      },
      {
        title: "对生",
        key: "对生",
      },
      {
        title: "轮生",
        key: "轮生",
      },
      {
        title: "基生叶",
        key: "基生叶",
      },
      {
        title: "茎生叶",
        key: "茎生叶",
      },
    ],
  },
];
export default {
  data() {
    return {
      expandedKeys1: ["花"],
      expandedKeys2: ["叶"],
      autoExpandParent: true,
      checkedKeys1: [],
      selectedKeys1: [],
      checkedKeys2: [],
      selectedKeys2: [],
      treeData1,
      treeData2,
      listData: [],
      nameList: [],
      name:[],
      temp:[],
      pagination: {
        onChange: (page) => {
          console.log(page);
        },
        pageSize: 3,
      },
    };
  },
  watch: {
    checkedKeys1(val) {
      console.log("onCheck", val);
      this.selectedKeys1 = val;
      var cats_flower = {
        selected_f: this.selectedKeys1,
      };
      this.axios
        .post(
          "http://127.0.0.1:5000/flower_fea",
          JSON.stringify(this.selectedKeys1)
        )
        .then((body) => {
          this.temp=[]
          console.log(body.data);
          if (this.listData.length==0){
                this.temp=body.data
              }
          else{
            for (var i in body.data) {
            for(var j in this.listData){
                if (body.data[i]==this.listData[j]){
                this.temp.push(body.data[i])
              }
            }
          }
          }
          
          console.log(this.temp)
          this.listData=this.temp
        });
    },
    checkedKeys2(val) {
      console.log("onCheck", val);
      this.selectedKeys2 = val;
      var cats_flower = {
        selected_l: this.selectedKeys2,
      };
      this.axios
        .post(
          "http://127.0.0.1:5000/flower_fea",
          JSON.stringify(this.selectedKeys2)
        )
        .then((body) => {
          this.temp=[]
          console.log(body.data);
          if (this.listData.length==0){
                this.temp=body.data
              }
          else{
            for (var i in body.data) {
            for(var j in this.listData){
                if (body.data[i]==this.listData[j]){
                this.temp.push(body.data[i])
              }
            }
          }
          }
          console.log(this.temp)
          this.listData=this.temp
        });
    },
  },
  methods: {
    postDetail(value){
      console.log(value);
      this.name.push(value)
      this.axios
        .post(
          "http://127.0.0.1:5000/curr_name",
          JSON.stringify(this.name)
        )
        .then((body) => {
          console.log(body.data);
          // this.listData = body.data;
        });
    },
    onSearch(value) {
      console.log(value);
      this.nameList = [{ name: value }];
      this.axios
        .post(
          "http://127.0.0.1:5000/flower_name",
          JSON.stringify(this.nameList)
        )
        .then((body) => {
          console.log(body.data);
          this.listData = body.data;
        });
    },
    onExpand1(expandedKeys) {
      // console.log('onExpand', expandedKeys1);
      // if not set autoExpandParent to false, if children expanded, parent can not collapse.
      // or, you can remove all expanded children keys.
      this.expandedKeys1 = expandedKeys;
      this.autoExpandParent = false;
    },
    onCheck1(checkedKeys) {
      console.log("onCheck", checkedKeys1);
      this.checkedKeys1 = checkedKeys;
    },
    onSelect1(selectedKeys, info) {
      console.log("onSelect", info);
      this.selectedKeys1 = selectedKeys;
    },
    onExpand2(expandedKeys) {
      console.log("onExpand", expandedKeys);
      // if not set autoExpandParent to false, if children expanded, parent can not collapse.
      // or, you can remove all expanded children keys.
      this.expandedKeys2 = expandedKeys;
      this.autoExpandParent = false;
    },
    onCheck2(checkedKeys) {
      console.log("onCheck", checkedKeys);
      this.checkedKeys2 = checkedKeys;
    },
    onSelect2(selectedKeys, info) {
      console.log("onSelect", info);
      this.selectedKeys2 = selectedKeys;
    },
  },
};
</script>
<style scope>
.ant-breadcrumb {
  font-size: 16px;
}

.ant-divider-horizontal {
  margin-top: 16px !important;
  margin-bottom: 16px !important;
}
.trees {
  margin-top: 20px;
  display: flex;
  width: 30%;
  justify-content: space-around;
}
.alist {
  width: 70%;
  display: flex;
}
.atree_list {
  width: 89%;
  display: flex;
  justify-content: space-between;
}
.ant-list-empty-text {
  position: relative;
  left: 250px;
}
.ant-list-pagination {
  position: relative;
  left: 250px;
}
</style>